export default {
	data() {
		return {
			// 表单
			name: '',
			brief: '',
			gameType: '-1',
			// 模态框
			modalName: null,
			// 活动级别
			gameTypeList: ['院级', '校级', '市级', '省级', '国家级', '国际', '其他'],
			popupTypeFlag: false,
			// 上传图片	{url: 'https://img.yzcdn.cn/vant/leaf.jpg', name: '图片1'}
			picList: [{
				url: 'https://img.yzcdn.cn/vant/leaf.jpg',
				name: '图片1'
			}],
			award: []
		};
	},
	onLoad() {
		console.log("-----")
		uni.getStorage({
			key: 'award',
			success: (res => {
				this.award = res.data
				this.name = this.award.name
				this.brief = this.award.brief
				this.gameType = this.award.type
				uni.removeStorageSync('award')
			})
		})
		
	},
	methods: {
		navTo(url) {
			uni.navigateTo({
				url
			})
		},
		// 模态框
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		RadioChange(e) {
			console.log(e)
			this.gameType = e.detail.value
			this.hideModal()
		},
		async addAward(){
			if(this.name == "" || this.brief == "" || this.gameType == "-1"){
				this.$api.msg("请完善信息")
				return;
			}
			uni.showLoading()
			const res = await this.$api.request({
				url: '/user/addAward',
				method: "PUT",
				contentType: "application/json",
				data: {
					name: this.name,
					brief: this.brief,
					type: this.gameType
				}
			})
			if(res.status == 200){
				this.returnPage("添加成功~")
			}
			
		},
		async editAward(){
			if(this.name == "" || this.brief == "" || this.gameType == "-1"){
				this.$api.msg("请完善信息")
				return;
			}
			uni.showLoading()
			const res = await this.$api.request({
				url: '/user/editAward/' + this.award.id,
				method: "PUT",
				contentType: "application/json",
				data: {
					name: this.name,
					brief: this.brief,
					type: this.gameType
				}
			})
			if(res.status == 200){
				this.returnPage("修改成功~")
			}
			
		},
		async delAward(){
			uni.showLoading()
			const res = await this.$api.request({
				url: '/user/delAward/' + this.award.id
			})
			if(res.status == 200){
				this.returnPage("删除成功~")
			}			
		},
		returnPage(msg){
			setTimeout(()=>{
				uni.hideLoading()
				this.$api.prePage().loadData()
				uni.navigateBack()
				this.$api.msg(msg)
			}, 1000)
		},
		
		
		
		// 上传图片
		fileIsEmpty() {
			if (this.picList.length == 0) {
				return true
			} else {
				return false
			}
		},
		afterReadPic(e) {
			console.log(e)
			console.log(e.detail)
			console.log(e.detail.file.path)
			const file = e.detail.file
			const test = {
				'url': file.path
			}
			console.log(test)
			this.picList.push(test)
			// const { file } = event.detail;
			// 当设置 mutiple 为 true 时, file 为数组格式，否则为对象格式
			// wx.uploadFile({
			//   url: 'https://example.weixin.qq.com/upload', // 仅为示例，非真实的接口地址
			//   filePath: file.path,
			//   name: 'file',
			//   formData: { user: 'test' },
			//   success(res) {
			//     // 上传完成需要更新 picList
			//     const { picList = [] } = this.data;
			//     picList.push({ ...file, url: res.data });
			//     this.setData({ picList });
			//   }
			// });
		},
		beforeRead(e) {
			console.log(e)
		},
		deletePic(e) {
			this.picList.pop(e.detail.index)
			this.$api.msg("删除成功")
		}
	}
}
